@import "../_variables";

@import "~bootstrap/scss/tables";

img,
.captionImage,
.image,
.ss-htmleditorfield-file {
    display: block;
    margin: 1rem;
    max-width: 100%;

    &.center {
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }

    &.left {
        float: left;
        clear: left;
        margin: 0 1rem 1rem 0;
    }

    &.right {
        float: right;
        clear: right;
        margin: 1rem 0 0 1rem;
    }

    &.leftAlone {
        float: left;
        clear: left;
        margin: 0 1rem 1rem 0;
    }

    &.rightAlone {
        float: right;
        clear: right;
        margin: 1rem 0 0 1rem;
    }

    iframe {
        width: 100% !important;
    }
}

.text-left {
    text-align: left;
}

.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
}

.text-justify {
    text-align: justify;
}

table {
    width: 100%;
    max-width: 100%;
    border-collapse: collapse;

    @extend .table;

    @extend .table-bordered;
}

table {
    &.table-none {
        border: 0;

        tr, td, th {
            border: 0;
            background: none !important;
        }
    }
}

ul {
    list-style: none;

    li {
        list-style: none;

        &:before {
            content: "\2022";
            display: inline-block;
            margin-right: .5em;
        }
    }
}

iframe {
    max-width: 100%;
}
